ver {\n  text-decoration: underline;\n  cursor: pointer;\n}\n\n.details-title {\n  text-transform: uppercase;\n  font-size: 0.7em;\n  color: var(--lightest-black);\n}\n\n.restaurant-address {\n  color: var(--lighter-black);\n  font-size: 0.8em;\n}\n\n.restaurant-rating {\n  position: absolute;\n  top: 0;\n  right: 0;\n  /* background-color: #fbc3fb; */\n  padding: 0.6em;\n  border-radius: 3px;\n  background: linear-gradient(var(--lighter-purple), var(--lightest-purple));\n  color: var(--lighter-grey);\n  font-weight: bold;\n}\n\n.details-description {\n  text-transform: capitalize;\n  font-size: 0.7em;\n}\n\na {\n  text-decoration: none;\n}\n\n.view-details-div {\n  text-transform: uppercase;\n  color: var(--lighter-grey);\n  background-color: var(--light-purple);\n  padding: 1em;\n  text-align: center;\n}\n\n.view-details-div:hover {\n  cursor: pointer;\n  color: var(--dark-grey);\n}\n\n/* Filter Restaurant related */\n.filter-restaurants {\n  display: grid;\n  grid-template-columns: 1fr;\n  background-color: var(--lighter-grey);\n  border: solid 1px var(--dark-grey);\n  border-radius: 5px;\n  overflow-y: auto;\n}\n\n.filter-info {\n  text-transform: capitalize;\n  padding: 1em;\n}\n\n.filter-name {\n  font-size: 1em;\n}\n\n.filter-title {\n  text-transform: capitalize;\n}\n\n.item-info {\n  display: grid;\n  grid-template-columns: 2fr 1fr;\n  padding: 0.4em;\n  font-size: 0.8em;\n  cursor: pointer;\n}\n\n.item-count {\n  color: var(--lightest-black);\n}\n\n/* both active item have same color and font */\n.active-item,\n.active-item-sort {\n  color: var(--light-purple);\n  font-weight: bold;\n}\n\n.active-item:hover {\n  text-decoration: line-through;\n}\n\n/* Loading image related */\n.loading-div {\n  display: grid;\n  color: var(--light-purple);\n  font-size: 4em;\n  align-items: center;\n  justify-items: center;\n}\n\n.loading-gif {\n  width: 100px;\n}\n\n/* hide div related */\n.hide-div {\n  display: none;\n}\n\n/* Each restaurant info related */\n.restaurant